.ul {
  vertical-align: top;
  overflow: visible;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.ul li {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  border: 1px solid #c8dde9;
  text-align: center;
  width: 40px;
  line-height: 40px;
  height: 40px;
  overflow: visible; }

.ul li div {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #c8dde9; }

.ul li div input {
  outline: none;
  border: none;
  height: 38px;
  width: 50px;
  padding-left: 10px; }

.ul li div input + input {
  padding: 0;
  cursor: pointer;
  border-left: 1px solid #c8dde9;
  width: 40px; }

.ul li div input + input:hover {
  background-color: #c8dde9; }

.ul li:hover {
  cursor: pointer;
  background-color: #c8dde9; }

.ul li:last-child {
  border: none;
  width: auto;
  height: 41px;
  vertical-align: top;
  background-color: transparent; }

.ul li input {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 40px;
  line-height: -10px; }

.ul li:last-child:hover,
.ul li[name="..."] {
  cursor: default;
  background-color: transparent; }

.active {
  background-color: #c8dde9; }



