@-webkit-keyframes sileUpIn {
  0% {
    opacity: 0;
    -webkit-transform: scaleY(0.8);
    transform: scaleY(0.8); }
  100% {
    opacity: 1;
    -webkit-transform: scaleY(1);
    transform: scaleY(1); } }

@keyframes sileUpIn {
  0% {
    opacity: 0;
    -webkit-transform: scaleY(0.8);
    transform: scaleY(0.8); }
  100% {
    opacity: 1;
    -webkit-transform: scaleY(1);
    transform: scaleY(1); } }

.select-container {
  position: relative;
  display: inline-block;
  list-style: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.select-container div {
  cursor: pointer;
  border: 1px solid #000;
  padding: 1px 3px; }

.select-container .ul {
  -webkit-animation: slideUpIn .3 ease;
  animation: slideUpIn .3 ease;
  position: absolute;
  list-style: none;
  margin: 0;
  padding: 0; }

.select-container .ul .li {
  border: 1px solid #000;
  border-top: none;
  padding: 1px 3px;
  white-space: nowrap;
  background-color: #fff; }

.select-container .ul .li:hover {
  cursor: pointer;
  background-color: #555; }

.hide {
  display: none; }

div {
  outline: none; }



