table {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: table;
  border-color: #d9ebf7;
  border-collapse: collapse;
  text-align: left !important; }

table tbody tr:nth-child(odd) {
  background-color: #f9fbff; }

table th,
table td {
  position: relative;
  border: 1px solid #d9ebf7;
  vertical-align: middle;
  padding: 8px;
  padding-right: 20px;
  text-align: left; }

table th:hover {
  cursor: pointer;
  border-bottom: 1px solid #468EB8 !important; }

table .up-tag {
  border-bottom: 1px solid #468EB8; }

table .up-tag:after {
  display: inline-block;
  text-align: right;
  content: '^';
  position: absolute;
  right: 5px;
  color: #468EB8;
  font-size: 18px; }

table .down-tag {
  border-bottom: 1px solid #468EB8; }

table .down-tag:after {
  display: inline-block;
  text-align: right;
  content: 'v';
  position: absolute;
  right: 5px;
  color: #468EB8;
  font-size: 18px; }



