.todoContainer {
  display: inline-block;
  list-style: none; }

.todoContainer li {
  position: relative;
  padding-right: 160px;
  text-align: left;
  white-space: nowrap; }

.todoContainer li div {
  display: -ms-flexbox;
  display: flex;
  width: 120px;
  position: absolute;
  right: 5px;
  top: 0px;
  padding: 0 10px; }

.todoContainer li div span {
  display: inline-block;
  width: 50px; }

.todoContainer li div div {
  display: inline-block;
  width: 60px;
  position: absolute;
  right: 0;
  top: 0px; }

.todoContainer li div div input[type="checkbox"] {
  display: none; }

.todoContainer li div div input[type="checkbox"] + label {
  cursor: pointer;
  display: inline-block;
  width: 40px;
  height: 20px;
  position: relative;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  margin: 0px 10px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-shadow: -3px 1px 8px rgba(200, 200, 200, 0.7);
  box-shadow: -3px 1px 8px rgba(200, 200, 200, 0.7); }

.todoContainer li div div input[type="checkbox"] + label:after,
.todoContainer li div div input[type="checkbox"] + label:before {
  content: '';
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 20px;
  height: 20px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer; }

.todoContainer li div div .flex-3:checked + label.green {
  background: #AEDCAE; }

.todoContainer li div div .flex-3:checked + label.green:after {
  background: #5CB85C; }

.todoContainer li div div .flex-3:checked + label:active:after {
  left: calc(100% - 28px); }

.todoContainer li div div .flex-3:checked + label:after {
  left: calc(100% - 18px); }

.todoContainer li div div .flex-3 + label {
  background: #ddd;
  border-radius: 20px; }

.todoContainer li div div .flex-3 + label:active:after {
  width: 26px; }

.todoContainer li div div .flex-3 + label:after {
  background: #fff;
  border-radius: 10px;
  width: 16px;
  height: 16px;
  top: 2px;
  left: 2px; }

.todoContainer li div div .green {
  background-color: #5CB85C; }



